<!-- 圈子内容详情 -->
<template>
	<view class="circleDetail2 flex-1">
		<circleDetailCartLzc :czlist="czlist" :isfu="false"></circleDetailCartLzc>
		<view class="CDfoot">
			<view class="cdf_tit dis_flex aic">
				<view class="dis_flex_c ju_c aic">
					<text class="cdf_text fs15 fw400">评价(152)</text>
					<view class="cdf_hr"></view>
				</view>
			</view>
			<scroll-view scroll-y="true" class="cdf_body" :style="{height:`${height}upx`}">
				<view class="cdf_body_item dis_flex ju_b" v-for="(item,index) in pList" :key="">
					<image :src="getimg(item.hpic)" mode="widthFix"></image>
					<view class="cdf_body_iv dis_flex_c" style="width: 100%;">
						<view class="dis_flex ju_b">
							<text class="tel fs12">{{item.tel}}</text>
							<view class="fs13 myD3" @tap.stop="zan(index)">
								<text class="iconfont icon-zan-" v-if="item.iszan"></text>
								<text class="iconfont icon-zan" style="color: #F43B31;" v-if="item.iszan==false"></text>
								<text class="pinNum" :style="{color:item.iszan?'#ddd':'#F43B31'}">{{item.num}}</text>
							</view>
						</view>
						<text class="con fs12 myC3 fw400 oh2">{{item.con}}</text>
					</view>
				</view>


			</scroll-view>
			<view class="cdfBtn dis_flex ju_c aic">
				<input class="cdfBtnv fs12" cursor-spacing=20 placeholder="请输入您要评论的内容…" v-model="pinText"
					placeholder-style="font-size:24upx;">
				<view class="btnCdf dis_flex aic ju_c" @click.stop="send">
					发送
				</view>
				</input>
			</view>
		</view>
	</view>
</template>

<script>
	import api from '../../components/service.js';
	import circleDetailCartLzc from '@/components/circle-detail-cart-lzc/circle-detail-cart-lzc.vue'
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		components: {
			circleDetailCartLzc
		},
		data() {
			return {
				pinText:'',
				czlist: [{
					hpic: '/static/images/logo.png',
					tel: '1535725603',
					time: '刚刚',
					con: '公司注册时开始创业的第一步，当我们有了项目，开始创业时，会需要用到公司的营业执照及相关资质证书，例如：入住天猫、京东等都需要提供营业执照；跟合作伙伴签合同时',
					imgs: [{
						img: 'https://ns-strategy.cdn.bcebos.com/ns-strategy/upload/applvyou/part-002139-1775.jpg'
					}],
					pNum: 200
				}],
				height: 0,
				pList: [{
						hpic: '/static/images/logo.png',
						tel: '1535725603',
						num: 67,
						con: '公司注册时开始创业的第一步，当我们有了项目，开始创业时，会需要用到公司的营业执照及相关资质证书，例如：入住天猫、京东等都需要提供营业执照；跟合作伙伴签合同时',
						iszan: true
					},
					{
						hpic: '/static/images/logo.png',
						tel: '1535725603',
						num: 67,
						con: '公司注册时开始创业的第一步，当我们有了项目，开始创业时，会需要用到公司的营业执照及相关资质证书，例如：入住天猫、京东等都需要提供营业执照；跟合作伙伴签合同时',
						iszan: true
					}
				]
			}
		},
		onReady() {
			this.height = uni.getSystemInfoSync().windowHeight * 2 - 834;
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			send(){
				console.log(this.pinText);
				uni.showToast({
					title:'留言成功',
					success: () => {
						this.pinText = '';
					}
				})
			},
			zan(index) {
				for (var i = 0; i < this.pList.length; i++) {
					if (i == index) {
						this.pList[i].iszan = !this.pList[i].iszan
						if(this.pList[i].iszan){
							this.pList[i].num --;
						}else{
							this.pList[i].num ++;
						}
					}
				}
				// if(this.iszan){
				// 	this.znum --;
				// }else{
				// 	this.znum ++;
				// }
				// console.log(111)
			}
		}
	}
</script>

<style scoped>
	.circleDetail2 {
