		background-color: #f8f8f8;
		box-sizing: border-box;
		padding-top: 10upx;
	}


	/deep/.tbitem_ll_img {
		height: 297upx !important;
	}

	/deep/.tbitem {
		height: 668upx !important;
		margin-bottom: 10upx;
		background-color: #fff;
		z-index: 99;
	}

	.CDfoot {
		width: 750upx;
		height: calc(100% - 668rpx);
		background-color: #fff;
		position: relative;
	}

	.cdf_tit {
		width: 100%;
		height: 68upx;
		padding-left: 61upx;
		position: relative;
		border-bottom: 1px solid #eee;
	}

	.cdf_body {
		width: 750upx;
		/* padding: 22upx 30upx; */
		box-sizing: border-box;
	}

	.cdf_text {
		color: #3c3c3c;
		line-height: 36upx;
	}

	.cdf_hr {
		width: 54upx;
		height: 4upx;
		background: #F43B31;
		position: absolute;
		bottom: 0;

	}

	.cdfBtn {
		width: 100%;
		height: 98upx;
		box-shadow: 0px -4upx 10upx 0px rgba(202, 202, 202, 0.2);
		/* padding-bottom: constant(safe-area-inset-bottom);
		padding-bottom: env(safe-area-inset-bottom);
		box-sizing: border-box; */
	}

	.cdfBtnv {
		width: 690upx;
		height: 72upx;
		background: #F8F8F8;
		border-radius: 36upx;
		position: relative;
		box-sizing: border-box;
		padding-left: 30upx;
		padding-right: 142upx;
	}

	.btnCdf {
		width: 142upx;
		height: 72upx;
		background: #F43B31;
		border-radius: 36upx;
		position: absolute;
		right: 30upx;
		color: #fff;
	}

	.cdf_body_item {
		padding: 22upx 30upx;
		border-bottom: 1px solid #eee;
	}

	.cdf_body_item image {
		width: 40upx;
		height: 40upx;
		border-radius: 50%;
		min-width: 40upx;

	}

	.tel {
		font-family: PingFang SC;
		color: #999999;
	}

	.cdf_body_iv {
		padding-left: 10upx;
		box-sizing: border-box;
	}

	.cdf_body_iv .icon-zan-, .cdf_body_iv .icon-zan{
		font-size: 26upx;
		color: #ddd;
	}

	.con {
		font-family: PingFang SC;
		line-height: 40upx;
		text-align: justify;
	}

	.myC3 {
		color: #333333;
	}

	.myD3 {
		color: #ddd;
	}
</style>
